<template>
    <main class="app-main">
        <section class="top">
            <p class="title"><md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>基本信息</p>
        </section>
        <div class="con-con">
            <textarea class="profileText" v-model="infoText" cols="2" rows="6" style="overflow:hidden"></textarea>
        </div>
        <md-button type="default" @click="saveText">保存</md-button>
    </main>
</template>

<script>
import { Field, CellItem, Icon, InputItem, Button } from 'mand-mobile'
export default {
  name: 'index',
  components: {
    [Field.name]: Field,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [InputItem.name]: InputItem
  },
    data() {
      return {
          infoText: ''
      }
    },
  methods: {
    // 回退
    goBack () {
      this.$router.push('/personal/editInfo')
    },
    saveText () {
        this.$router.push('/personal/editInfo')
        window.localStorage.infoText = this.infoText
      }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        overflow hidden
    .top
        .title
            background url("../../../assets/personal/bg.png") no-repeat
    .con-con
        width 90%
        margin 0px auto
        margin-top 118px
        margin-bottom 60px
        .profileText
            width 100%
            height 500px
            color #999
            padding 24px 33px
            font-size 32px
            background rgba(166,166,166,.1)
            box-shadow:0px 4px 24px 0px rgba(2,25,85,0.1);
            border-radius:12px;
    /deep/.md-button
        width:660px;
        height:88px;
        background:rgba(2,25,85,1);
        border-radius:12px;
        margin 0 auto
        color #fff
</style>
